import React, { useEffect, useState } from "react";

// component
import importAll from "../components/Functions/ImportAll";
import { Container, Row, Col, Image, Button } from "react-bootstrap";
import { isMobile } from "react-device-detect";
import axios from "axios";
import { v4 as uuidv4 } from "uuid";

export default function PageSchool() {
  const [data, setData] = useState([]);
  const [dataList, setDataList] = useState([]);

  useEffect(() => {
    const schoolImages = importAll(
      require.context("../images/school/", false, /\.jpg|\.png|\.jpeg/)
    );

    const fetchData = () => {
      const desc =
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam congue molestie metus, sit amet semper erat feugiat id. Fusce facilisis felis sit amet libero eleifend tempus. Fusce aliquam sem vel porta blandit. Aenean maximus quam dignissim mi hendrerit, non sodales magna fringilla. Phasellus laoreet aliquam tellus nec tempor.";
      const det =
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam semper elit vitae massa dictum congue.";
      const inv =
        "Vestibulum ornare turpis rhoncus, varius sapien sit amet, rutrum justo. Nulla vehicula, lorem at rutrum placerat, odio est mattis lorem, quis rutrum dolor odio sed ipsum. Nunc tempus metus eu neque hendrerit, non aliquet lacus ornare. Phasellus varius tincidunt nulla id tempor. Ut gravida odio dolor, ullamcorper maximus metus sodales quis. Vestibulum ornare turpis rhoncus, varius sapien sit amet, rutrum justo. Nulla vehicula, lorem at rutrum placerat, odio est mattis lorem, quis rutrum dolor odio sed ipsum. Nunc tempus metus eu neque hendrerit, non aliquet lacus ornare. Phasellus varius tincidunt nulla id tempor. Ut gravida odio dolor, ullamcorper maximus metus sodales quis. Vestibulum ornare turpis rhoncus, varius sapien sit amet, rutrum justo. Nulla vehicula, lorem at rutrum placerat, odio est mattis lorem, quis rutrum dolor odio sed ipsum. Nunc tempus metus eu neque hendrerit, non aliquet lacus ornare. Phasellus varius tincidunt nulla id tempor. Ut gravida odio dolor, ullamcorper maximus metus sodales quis. Vestibulum ornare turpis rhoncus, varius sapien sit amet, rutrum justo.";
      const fot =
        "Etiam blandit nisl magna, sed consectetur augue viverra quis. Pellentesque non metus luctus, rhoncus sem at, aliquam metus. Praesent sit amet posuere turpis. Aliquam a tortor id sapien semper tristique. tiam blandit nisl magna, sed consectetur augue viverra quis. Pellentesque non metus luctus, rhoncus sem at, aliquam metus. Praesent sit amet posuere turpis. Aliquam a tortor id sapien semper tristique. tiam blandit nisl magna, sed consectetur augue viverra quis. Pellentesque non metus luctus, rhoncus sem at, aliquam metus. Praesent sit amet posuere turpis. Aliquam a tortor id sapien semper tristique. tiam blandit nisl magna, sed consectetur augue viverra quis. Pellentesque non metus luctus, rhoncus sem at, aliquam metus. Praesent sit amet posuere turpis. Aliquam a tortor id sapien semper tristique.";

      setData([
        {
          src: schoolImages[0],
          title: "Sunday School",
          details: det,
          description: desc,
          involve: inv,
          footer: fot,
          date: ["Monday", "Thursday"],
          time: "8:00PM - 10:00PM",
        },
      ]);
    };

    fetchData();

    const jsonFilter = JSON.stringify({
      content_category: { filter: "sunday school" },
    });

    axios
      .get(
        process.env.REACT_APP_API_URL + "/api/get_contents?filter=" + jsonFilter
      )
      .then((result) => {
        const data = result.data.result.data;
        setDataList(data?.content_url);
      });
  }, []);

  return (
    <>
      <Container fluid className="school-container m-0 p-0">
        <Row className="m-0 px-lg-5 w-100 h-auto justify-content-center">
          <Col
            lg={12}
            className={`${
              isMobile ? "w-100" : "w-75"
            } d-flex justify-content-center justify-content-lg-start`}
          >
            <h1 className="school-details-header">{data[0]?.title}</h1>
          </Col>
          <Col
            lg={12}
            className={`${
              isMobile ? "w-100" : "w-75"
            } d-flex justify-content-center justify-content-lg-start`}
          >
            <small className="school-details-small">{data[0]?.details}</small>
          </Col>
        </Row>
      </Container>
      <Container fluid className="m-0 p-0 mt-lg-3 w-100 h-auto">
        <Row lg={6} className="m-0 px-lg-5 w-100 h-100 justify-content-center">
          <Col lg={12} className={`${isMobile ? "w-100" : "w-75"} m-0 p-0`}>
            <Row className="m-0 p-0 h-100">
              <Col lg={6} className={`${isMobile ? "order-2" : ""}`}>
                <Row>
                  <Col lg={12}>
                    <small className="school-details-small">
                      {data[0]?.description}
                    </small>
                  </Col>
                  <Col lg={12}>
                    <h1 className="school-get-involved pt-3">GET INVOLVED</h1>
                  </Col>
                  <Col lg={12}>
                    <small className="school-details-small">
                      {data[0]?.involve}
                    </small>
                  </Col>
                </Row>
              </Col>
              <Col lg={6} className={`${isMobile ? "order-1" : ""}`}>
                <Row>
                  <Col lg={12}>
                    <Image
                      className="school-image-list py-3"
                      src={data[0]?.src}
                    />
                  </Col>
                </Row>
              </Col>
              <Col lg={8} className={`${isMobile ? "order-3" : ""} mt-lg-5`}>
                <Row>
                  <Col lg={12}>
                    <small className="school-details-small">
                      {data[0]?.footer}
                    </small>
                  </Col>
                  {dataList?.content_url !== undefined ? (
                    <>
                      <Col
                        key={uuidv4()}
                        lg={12}
                        className="d-flex justify-content-center p-5"
                      >
                        <Button
                          size="lg"
                          className="button-all-sermon"
                          href="/Events"
                        >
                          Join Us
                        </Button>
                      </Col>
                    </>
                  ) : (
                    <></>
                  )}
                </Row>
              </Col>
              <Col lg={4} className="pt-3 pt-lg-0">
                <Container className="m-0 p-0 school-details-shadow">
                  <Row className="m-0 p-0 ps-4 pt-5 w-100">
                    <Col
                      lg={12}
                      className="school-details-border d-flex align-items-center"
                    >
                      <h1 className="school-details-header-txt">Schedule</h1>
                    </Col>
                  </Row>
                  <Row className="m-0 p-0 ps-4 pt-3 w-100">
                    <Col lg={12} className="school-details-text-border">
                      <ul className="m-0 p-0">
                        <li className="d-flex justify-content-between">
                          <p className="school-details-text">Days: </p>
                          <span className="">
                            {data[0]?.date?.map((dataDate, index) => {
                              let output = "";

                              if (index + 1 !== data[0].date?.length) {
                                output = dataDate + ", ";
                              } else {
                                output = dataDate;
                              }

                              return output;
                            })}
                          </span>
                        </li>
                        <li className="d-flex justify-content-between">
                          <p className="school-details-text">Time:</p>
                          <span className="">{data[0]?.time}</span>
                        </li>
                        <li className="d-flex justify-content-between">
                          <p className="school-details-text">Phone:</p>
                          <span className="">9089089890</span>
                        </li>
                        <li className="d-flex justify-content-between">
                          <p className="school-details-text">Email:</p>
                          <span className="">test@gmail.com</span>
                        </li>
                      </ul>
                    </Col>
                  </Row>
                </Container>
              </Col>
            </Row>
          </Col>
        </Row>
      </Container>
      <Container fluid className="m-0 p-0 h-15"></Container>
    </>
  );
}
